import React from "react";
import "../translations/i18n";
import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { useTranslation } from "react-i18next";

const displayRoles = () => {
  const { t } = useTranslation();
  const profileData: any = t("profileData:profile", { returnObjects: true });

  const timelineItems: any[] = [];

  profileData.roles.forEach(
    (role: any) =>
      timelineItems.push(timelineItem(
        new Date(role.startContractDate),
        new Date(role.endContractDate),
        role.position,
        role.jobDescription
      ))
  );

  return (
    <div>
      {timelineItems}
    </div>
  );
};

const timelineItem = (
  startDate: Date,
  endDate: Date,
  position: string,
  jobDescription: string
) => {
  return (
    <TimelineItem>
      <TimelineOppositeContent>
        <Typography variant="body2" color="textSecondary">
          {endDate.getFullYear()}/{endDate.getMonth() + 1} - {startDate.getFullYear()}/{startDate.getMonth() + 1}
        </Typography>
      </TimelineOppositeContent>
      <TimelineSeparator>
        <TimelineDot />
        <TimelineConnector />
      </TimelineSeparator>
      <TimelineContent>
        <Paper elevation={3} style={{ padding: "6px 16px" }}>
          <Typography variant="h6" component="h1">
            {position}
          </Typography>
          <Typography>
            {jobDescription}
          </Typography>
        </Paper>
      </TimelineContent>
    </TimelineItem>
  );
};

export const Roles = () => {
  return (
    <section className="section">
      <Timeline align="alternate">
        {displayRoles()}
      </Timeline>
    </section>
  );
};
